                            <el-table
                                    :data="knowledge.slice((paging.page - 1) * paging.pageSize, paging.page * paging.pageSize)"
                                    style="width: 100%">
                                <el-table-column
                                        label=""
                                        width="100%">
                                    <template slot-scope="scope">
                                        <li @click="toDetail(scope.row.uidpk)">
                                            <a href="javascript:void(0);">
                                                <img :src="scope.row.articleThumbnailBean.thumbnailurl" alt="">
                                                <div class="msg">
                                                    <h2>{{ scope.row.title }}</h2>
                                                    <div class="msg-label">热门</div>
                                                    <p class="cont">{{ scope.row.description | slh(50) }}</p>
                                                    <div class="view">
