<script>

    import Swiper from "swiper"
    import tabNav from "@/components/tabBar/tabBar"
    import newsRight from "@/components/newsRight/newsRight"
    import foot from "@/components/foot/foot"
    import floating from '@/components/floating/floating.vue';

    export default {
        name: "News",
        data() {
            return {
                activeName: "1",
                // 文章数据
                newsList: [],
                // 文章总数
                total: "",
                // // 分页设置
                paging: {
                    // 当前页数
                    page: 1,
                    // 每页条数
                    pageSize: 8,
                    // 总页数
                    total: 1,
                },

                // 最新入驻 9
                hot: [],

                // 行业资讯
                // 行业动态 8
                dynamics: [],

                // 创业知识 14
                knowledge: [],

                // 创业故事 15
                story: [],
            }
        },
        methods: {
            lbtInit() {
                new Swiper('.swiper-container', {
                    loop: true, // 循环模式选项

                    // 如果需要分页器
                    // pagination: {
                    //     el: '.swiper-pagination',
                    // },
                    //
                    // // 如果需要前进后退按钮
                    // navigation: {
                    //     nextEl: '.swiper-button-next',
                    //     prevEl: '.swiper-button-prev',
                    // },
                    //
                    // // 如果需要滚动条
                    // scrollbar: {
                    //     el: '.swiper-scrollbar',
                    // },
                })
            },

            // 获取行业动态
            getHot() {
                this.axios.get(this.api.getNewsApi + "9")
                    .then(res => {
                        // console.log(res.data)
                        this.hot = res.data;
                        this.total = res.data.length
                    })
                    .catch(err => {
                        this.$message({
                            type: "error",
                            message: "请刷新重试!"
                        })
                    })
            },

            // 获取行业动态
            // 获取行业动态
            getDynamics() {
                this.axios.get(this.api.getNewsApi + "8")
                    .then(res => {
                        // console.log(res.data)
                        if (res.status === 200) {
                            this.dynamics = res.data
                            this.total = res.data.length
                        }
                    })
                    .catch(err => {
                        this.$message({
                            type: "error",
                            message: "请刷新重试!"
                        })
                    })
            },

            getKnowledge() {
                this.axios.get(this.api.getNewsApi + "14")
                    .then(res => {
                        // console.log(res.data)
                        if (res.status === 200) {
                            this.knowledge = res.data
                            this.total = res.data.length
                        }
                    })
                    .catch(err => {
                        this.$message({
                            type: "error",
                            message: "请刷新重试!"
                        })
                    })
            },

            getStory() {
                this.axios.get(this.api.getNewsApi + "15")
                    .then(res => {
                        // console.log(res.data)
                        if (res.status === 200) {
                            this.story = res.data
                            this.total = res.data.length
                        }
                    })
                    .catch(err => {
                        this.$message({
                            type: "error",
                            message: "请刷新重试!"
                        })
                    })
            },

            getAll() {
                this.getHot()
                this.getDynamics()
                this.getKnowledge()
                this.getStory()
            },

            // 页码改变
            handleSizeChange() {

            },

            //
            handleCurrentChange(val) {
                this.paging.page = val;
                // console.log(this.paging.page)
                // console.log(this.paging)
                // this.getData()
                document.documentElement.scrollTop = "100"

            },

            // 跳转到新闻详情页面
            toDetail(id) {
                // console.log(id);

                this.$router.push({
                    name: "newsDetails",
                    params: {
                        sid: id
                    }
                })
            },

            // 根据分类查找文章
            getListNews(e) {
                // console.log(e.target)
                // console.log(e.target.getAttribute("id").split("-")[1])
                // var thisList = e.target.getAttribute("id").split("-")[1] ||e.target.getAttribute("data-class")
                // console.log(thisList)
                let thisList;
                // thisList = e.target.getAttribute("data-class")
                if (e.target.getAttribute("data-class") === null) {
                    thisList = e.target.getAttribute("id").split("-")[1]
                } else {
                    thisList = e.target.getAttribute("data-class")
                }
                // console.log(thisList)
            },
        },
        mounted() {
            this.lbtInit()
        },
        created() {
            this.getAll();
            this.activeName = this.$route.params.list
        },
        components: {
            tabNav,
            newsRight,
            foot,
            floating
        }
    }
</script>

<style lang="less">
    @import "../../../node_modules/swiper/dist/css/swiper.min.css";

