                    }

                    .msg {
                        flex: 0 0 406px;
                        width: 406px;
                        padding-top: 21px;

                        h2 {
                            font-size: 20px;
                            color: #1e2629;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                        }

                        .msg-label {
                            width: 51px;
