                            border-radius: 9px;
                            border: solid 1px #ff1734;
                            color: #ff0b0f;
                            line-height: 18px;
                            text-align: center;
                            margin-top: 13px;
                            margin-bottom: 9px;
                        }

                        .cont {
                            font-size: 14px;
                            color: #4f646a;
                            width: 406px;
                        }

                        .view {
                            margin-top: 10px;

                            span {
                                text-align: left;
                                line-height: 24px;
                                color: #8aa0a6;

                                &:nth-of-type(1) {

                                    i {
                                        vertical-align: middle;
                                        margin: 10px 10px 10px 0;
                                    }
