<template>
    <div class="floating clearfix" ref="floating">

        <img src="./images/left_img.png" alt="">

        <div class="clearfix" id="user-form">
            <el-form :model="userForm" :rules="userRules" ref="ruleForm" class="user-form" :inline="true">
                <el-form-item label="">
                    <el-input v-model="userForm.username" placeholder="您的姓名:"></el-input>
                </el-form-item>
                <el-form-item label="" prop="userPhone">
                    <el-input v-model="userForm.userPhone" placeholder="您的电话:"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="userForm.address" placeholder="加盟区域:" class="user-desc"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')">
                        免费获取资料
                    </el-button>
                </el-form-item>
            </el-form>
        </div>

    </div>
</template>

<script>

    const qs = require('qs');

    export default {
        name: "floating",
        data() {
            var phoneCheck = (rule, value, callback) => {
                var reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/
                if (!value) {
                    callback(new Error('请输入您的联系方式'));
                } else if (!reg.test(value)) {
                    callback(new Error('请输入正确的联系方式'));
                } else {
                    callback()
                }
            };
            return {
                // 表单信息
                userForm: {
                    // 用户名，
                    username: "",
                    // 电话
                    userPhone: "",
                    // 加盟区域 或 留言
                    address: ""
                },

                userRules: {
                    userPhone: [
                        {validator: phoneCheck, trigger: 'blur'}
                    ]
                },
                // 当前url
                nowUrl: "",
                // 品牌 综合 4
                description: this.sort,
                // 设备标志 ( pc )
                userEqt: 0,

                userIp: ""
            }
        },
        methods: {
            // 弹窗
            floating() {
                // document.documentElement.onscroll(function () {
                //     console.log(document.documentElement.scrollTop())
                // })
                // console.log(this.$refs.floating)
                // console.log(document.documentElement.scrollTop)
                if (document.documentElement.scrollTop > 1000) {
                    this.$refs.floating.classList.add("showHeight")
                } else {
                    this.$refs.floating.classList.remove("showHeight")
                }
            },
            // 获取ip和地址
            getMsg() {
                this.userIp = returnCitySN["cip"];
                this.nowUrl = window.location.href;
            },
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (!valid) {
                        this.$message({
                            message: '请输入正确的号码后重试',
                            type: 'error'
                        });
                        return false
                    } else {
                        // this.axios.post(this.api.nxdFromApi + "?tell=" + this.userForm.userPhone + "&category=" + this.description + "&ip=" + this.userIp + "&description=" + this.userForm.address + "&hasComputer=" + this.userEqt + "&name=" + this.userForm.username + "&url=" + this.nowUrl)

                        this.axios.post(this.api.fromApi, qs.stringify({
                            uname: this.userForm.username,
                            phone: this.userForm.userPhone,
                            district: this.userForm.address,
                            url: this.nowUrl,
                            ip: this.userIp,
                            category: this.description,
                            hasComputer: this.userEqt
                        }))
                            .then(res => {
                                console.log(res.data.message);
                                if (res.data.message === 200) {
                                    this.$message({
                                        message: '提交成功，我们会尽快联系您',
                                        type: 'success',
                                    });
                                    Object.keys(this.userForm).forEach(key => this.userForm[key] = "")
                                } else {
                                    const h = this.$createElement;
                                    this.$notify.error({
                                        title: '',
                                        position: 'top-right',
                                        duration: 2000,
                                        message: h('i', {style: 'color: red'}, '错误，请重试')
                                    });
                                }
                            })
                    }
                })
            },

            headerHide() {
                // console.log(this.$refs.header)
            },
        },
        mounted() {
            this.floating()
            this.getMsg()
            // this.headerHide()
            window.addEventListener('scroll', this.floating, true)
        },
        created() {

        },
        props: ["sort"]
    }
</script>

<style lang="less">

    @import "../../assets/font/SourceHanSansK-Normal.css";

    .showHeight {
        height: 114px !important;
        opacity: 1 !important;
    }

    .floating {
        width: 100%;
        position: fixed;
        opacity: 0;
        bottom: -1px;
        left: 0;
        height: 0;
        background: url("./images/floating_back.png") no-repeat bottom center;
        background-size: contain;
        transition: all .5s ease-in-out;
        z-index: 999;
        padding-left: 126px;
        padding-right: 126px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        #user-form {
            margin-left: 56px;
            padding-bottom: 0;

            .user-form {
                display: flex;
                align-items: center;
            }

            .el-form-item {
                margin-bottom: 0;
                margin-right: 16px !important;
            }


            .el-input__inner {
                border: 1px solid #999;
                border-radius: 0;
                color: #333;
                height: 50px;
                font-size: 16px;
                font-weight: 600;

                &:focus {
                    border: 1px solid #333;
                }
            }

            input::-webkit-input-placeholder {
                /* placeholder颜色  */
                color: #333;
                padding-left: 10px;
                font-family: "Source Han Sans K Normal";
            }

            .el-form-item__error {
                color: #fff;
                font-size: 16px;
            }

            textarea::-webkit-input-placeholder {
                /* placeholder颜色  */
                color: #333;
                padding-left: 10px;
            }

            .el-textarea__inner {
                border: 1px solid #999;
                font-size: 16px;
                color: #333;


                &:focus {
                    border-color: #333;
                }
            }

            .el-button.el-button--primary {
                /*width: 128px;*/
                height: 50px;
                background-color: #ffd450;
                border-color: #ffd450;
                border-radius: 16px;

                span {
                    font-size: 26px;
                    color: #230505;
                    font-family: "Source Han Sans K Normal";
                }
            }

            .el-message.el-message--success {
                min-width: 270px;
                max-width: 270px;
            }

            h2 {
                width: 256px;
                height: 100%;
            }
        }
    }


</style>
