<template>
    <div class="mb-right" id="r">
        <!--最新资讯-->
        <div class="mr-news">
            <div class="mrn-title">

            </div>
            <ul>
                <li v-for="(item,index) in newsList.slice(0,7)" :key="index" @click="toDetail(item.uidpk)">
                <a href="javascript:void(0);">{{ item.title }}</a>
                </li>

            </ul>
        </div>

        <!--品牌故事-->
        <div class="story">
            <!--品牌标题-->
            <div class="story-title">

            </div>
            <ul class="brand-box">
                <li>
                    <router-link to="/sem/nxd" tag="a">
                        <img src="./images/brand01.png" alt="">
                    </router-link>
                </li>
                <li>
                    <router-link to="/sem/zyw" tag="a">
                        <img src="./images/brand02.png" alt="">
                    </router-link>
                </li>
                <li>
                    <router-link to="/sem/wy" tag="a">
                        <img src="./images/brand03.png" alt="">
                    </router-link>
                </li>
                <li>
                    <router-link to="/sem/lhl" tag="a">
                        <img src="./images/brand04.png" alt="">
                    </router-link>
                </li>
            </ul>
        </div>


        <!--最新案例-->
        <div class="case">
            <!--标题-->
            <div class="selqa-title">

            </div>
            <ul class="sel-nav">
                <li v-for="(item,index) in newsList.slice(0,10)" :key="index" @click="toDetail(item.uidpk)">
                <a href="javascript:void(0);">{{ item.title }}</a>
                </li>
            </ul>
        </div>

        <!--人物专访-->
        <div class="interview">
            <!--标题-->
            <div class="interview-title">

            </div>
            <div>
                <img src="./images/person.png" alt="">
            </div>
            <ul class="interview-nav">
                <li v-for="(item,index) in newsList.slice(0,3)" :key="index" @click="toDetail(item.uidpk)">
                <a href="javascript:void(0);">{{ item.title }}</a>
                </li>
            </ul>
        </div>

    </div>
</template>

<script>
    export default {
        name: "newsRight",
        data() {
            return {
                newsList: [],
                newsDetail:[],
            }
        },
        methods: {
            // 获取新闻列表
            getRandomNews() {
                this.axios.get(this.api.getRandomNewsListApi)
                    .then(res => {
                        // console.log(res)
                        if (res.status === 200) {
                            this.newsList = res.data
                        }
                    })
                    .catch(err => {
                        this.$message({
                            type: "error",
                            message: "加载错误!"
                        })
                    })
            },
            // 跳转到新闻详情页面
            toDetail(id) {
                // console.log(id);

                this.$router.push({
                    name: "newsDetails",
                    params: {
                        sid: id
                    }
                })
            },
        },
        created() {
            this.getRandomNews()
        },
        mounted() {
        }
    }
</script>

